.card {
  background-color: rgba(77, 37, 0, 0.151);
  color: #F6F5F3;
  border: 12px solid #cb8f32;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  border-radius: 0.375rem;
}
.card p{
  font-weight: 600;
;
}
.card-title{
  text-shadow: 2px 2px #250a0c;
  /* border-bottom: 12px solid #cb8f32; */
  font-weight: 800;
  margin-top: 0.4rem;
  font-size: 24px;
}
.card:after{
  content: "";
  height: 70px;
}
.card:hover {
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}
